<template>
  <div class="main">
    <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
    <div id="myPie" :style="{width: '48%', height: '300px',display:'inline-block'}"></div>
    <div id="myBar" :style="{width: '48%', height: '300px',display:'inline-block'}"></div>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    data () {
      return {
        msg: ''
      }
    },
    mounted(){
      this.drawLine();
      this.drawPie();
      this.drawBar();
    },
    methods: {
      drawLine(){
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        myChart.setOption( {
          title : {
            text: '',
            subtext: ''
          },
          tooltip : {
            trigger: 'axis'
          },
          legend: {
            data:['','']
          },
          calculable : true,
          xAxis : [
            {
              type : 'category',
              boundaryGap : false,
              data : ['','','','','','','']
            }
          ],
          yAxis : [
            {
              type : 'value',
              axisLabel : {
                formatter: '{value}'
              }
            }
          ],
          series : [
            {
              name:'',
              type:'line',
              data:[11, 11, 15, 13, 12, 13, 10],
              markPoint : {
                data : [
                  {type : 'max', name: ''},
                  {type : 'min', name: ''}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: ''}
                ]
              }
            },
            {
              name:'',
              type:'line',
              data:[0, 2, 5, 3, 2, 8, 6],
              markPoint : {
                data : [
                  {name : '', value : -2, xAxis: 1, yAxis: -1.5}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name : ''}
                ]
              }
            }
          ]
        });
      },
      drawPie(){
        // domecharts
        let myPie = this.$echarts.init(document.getElementById('myPie'))
        // 
        myPie.setOption( {
          title : {
            text: '',
            subtext: '',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient : 'vertical',
            x : 'left',
            data:['','','','','']
          },

          calculable : true,
          series : [
            {
              name:'',
              type:'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                {value:335, name:''},
                {value:310, name:''},
                {value:234, name:''},
                {value:135, name:''},
                {value:1548, name:''}
              ]
            }
          ]
        });
      },
      drawBar(){
        // domecharts
        let myBar = this.$echarts.init(document.getElementById('myBar'))
        var placeHoledStyle = {
          normal:{
            barBorderColor:'rgba(0,0,0,0)',
            color:'rgba(0,0,0,0)'
          },
          emphasis:{
            barBorderColor:'rgba(0,0,0,0)',
            color:'rgba(0,0,0,0)'
          }
        };
        var dataStyle = {
          normal: {
            label : {
              show: true,
              position: 'insideLeft',
              formatter: '{c}%'
            }
          }
        };
        myBar.setOption({
          title: {
            text: '',
            subtext: 'From ExcelHome',
            sublink: 'http://e.weibo.com/1341556070/AiEscco0H'
          },
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 
              type : 'shadow'        // 'line' | 'shadow'
            },
            formatter : '{b}<br/>{a0}:{c0}%<br/>{a2}:{c2}%<br/>{a4}:{c4}%<br/>{a6}:{c6}%'
          },
          legend: {
            y: 55,
            itemGap : document.getElementById('myBar').offsetWidth / 8,
            data:['GML', 'PYP','WTC', 'ZTW']
          },
          grid: {
            y: 80,
            y2: 30
          },
          xAxis : [
            {
              type : 'value',
              position: 'top',
              splitLine: {show: false},
              axisLabel: {show: false}
            }
          ],
          yAxis : [
            {
              type : 'category',
              splitLine: {show: false},
              data : ['', '', '', '']
            }
          ],
          series : [
            {
              name:'GML',
              type:'bar',
              stack: '',
              itemStyle : dataStyle,
              data:[38, 50, 33, 72]
            },
            {
              name:'GML',
              type:'bar',
              stack: '',
              itemStyle: placeHoledStyle,
              data:[62, 50, 67, 28]
            },
            {
              name:'PYP',
              type:'bar',
              stack: '',
              itemStyle : dataStyle,
              data:[61, 41, 42, 30]
            },
            {
              name:'PYP',
              type:'bar',
              stack: '',
              itemStyle: placeHoledStyle,
              data:[39, 59, 58, 70]
            },
            {
              name:'WTC',
              type:'bar',
              stack: '',
              itemStyle : dataStyle,
              data:[37, 35, 44, 60]
            },
            {
              name:'WTC',
              type:'bar',
              stack: '',
              itemStyle: placeHoledStyle,
              data:[63, 65, 56, 40]
            },
            {
              name:'ZTW',
              type:'bar',
              stack: '',
              itemStyle : dataStyle,
              data:[71, 50, 31, 39]
            },
            {
              name:'ZTW',
              type:'bar',
              stack: '',
              itemStyle: placeHoledStyle,
              data:[29, 50, 69, 61]
            }
          ]
        });

      }
    }
  }
</script>

<style scoped>

</style>



