<template>
    <div class="main">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-form :inline="true" :model="searchForm" class="demo-form-inline">
          <el-col :span="6">
            <el-input v-model="searchForm.name" placeholder="">
              <template slot="prepend"></template>
            </el-input>
          </el-col>
          <el-col :span="6">
            <el-input v-model="searchForm.state" placeholder="">
              <template slot="prepend"></template>
            </el-input>
          </el-col>
          <el-button type="primary" icon="search" @click="searchClick"></el-button>
          <el-breadcrumb-item :to="{ path: '/welcome' }">
            <el-button type="info" plain></el-button>
          </el-breadcrumb-item>
        </el-form>
      </el-breadcrumb>
      <el-scrollbar class="scroll-area">
      <el-table
        :data="tableData"
        v-loading.body="loading"
        @selection-change="selectionChange"
        tooltip-effect="dark"
        style="width: 100%;"
        border
      >
        <el-table-column
          type="selection"
          width="35">
        </el-table-column>
        <el-table-column
          prop="orgName"
          label=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="type"
          label=""
          width="200"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="IP"
          label="IP"
          width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="code"
          label=""
          align="center"
          width="180">
        </el-table-column>
        <el-table-column
          prop="fwName"
          label=""
          align="center"
          width="180">
        </el-table-column>
        <el-table-column label=""
                         align="center"
                         width="180">
          <template scope="scope">
            <el-button class="editTextBtn" type="text" icon="el-icon-edit" size="mini" @click="editClick(scope.row)"></el-button>
            <el-button class="delTextBtn" type="text" icon="el-icon-delete" size="mini" @click="deleteClick(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
        <!---->
        <el-pagination background
                       @size-change="pageSizeChange"
                       @current-change="currentPageChange"
                       :current-page="currentPage"
                       :page-sizes="[10, 50, 100, 200]"
                       :page-size="pageSize"
                       layout="total, sizes, prev, pager, next, jumper"
                       :total="total">
        </el-pagination>
        <!---->
      </el-scrollbar>
      <!---->
      <el-dialog title="" v-model="editFormVisible" :visible.sync="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
          <el-form-item label="" prop="orgName">
            <el-input v-model="editForm.orgName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="editForm.type">
              <el-radio class="radio" :label="1"></el-radio>
              <el-radio class="radio" :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="IP">
            <el-input-number v-model="editForm.IP" :min="0" :max="200"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-input type="text" v-model="editForm.code"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="editForm.fwName"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false"></el-button>
          <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
    export default {
      data() {
        return {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          tableData: [
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },{
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            },
            {
              orgName: '',
              type: '',
              IP: '192.162.1.1',
              code: '80',
              fwName: 'FWMC'
            }],
          //
          loading: false,
          //
          searchForm: {
            id: '',
            name: '',
            state: ''
          },
          //
          multipleSelection: [],
          //
          currentPage: 3,
          //
          pageSize: 100,
          //
          total: 800,
          //
          deleteVisible: false,
          //
          editFormVisible: false,
          editLoading: false,
          editFormRules: {
            name: [
              {required: true, message: '', trigger: 'blur'}
            ]
          },
          //
          editForm: {
            id: 0,
            name: '',
            sex: -1,
            age: 0,
            birth: '',
            address: ''
          }
        }
      },
      methods: {
        //
        loadingData: function () {
          var _self = this;
          _self.loading = true;
          setTimeout(function () {
            console.info("");
            _self.loading = false;
          }, 300);
        },
        //
        editClick: function (row) {
          this.editFormVisible = true;
          this.editForm = Object.assign({}, row);
        },
        //
        deleteClick: function (row) {
          var _self = this;
          this.$confirm('' + row.orgName + '?', '', {
            type: 'warning'
          }).then(function () {
            _self.$message({
              message: row.orgName + '',
              type: 'success'
            });
            _self.loadingData();//
          }).catch(function (e) {
            if (e != "cancel")
              console.log("" + e);
          });
        },
        //
        addClick: function () {
          var _self = this;
          this.editFormVisible = true;
          //_self.loadingData();//
        },
        //
        searchClick: function () {
          alert("");
          var _self = this;
          _self.loadingData();//
        },
        //
        selectionChange: function (val) {
          for (var i = 0; i < val.length; i++) {
            var row = val[i];
          }
          this.multipleSelection = val;
          console.info(val);
        },
        //
        removeSelection: function () {
          var _self = this;
          var multipleSelection = this.multipleSelection;
          if (multipleSelection.length < 1) {
            _self.$message({
              message: '',
              type: 'error'
            });
            return;
          }
          var ids = "";
          for (var i = 0; i < multipleSelection.length; i++) {
            var row = multipleSelection[i];
            ids += row.name + ","
          }
          this.$confirm('' + ids + '?', '', {
            type: 'warning'
          }).then(function () {
            _self.$message({
              message: ids + '',
              type: 'success'
            });
            _self.loadingData();//
          }).catch(function (e) {
            if (e != "cancel")
              console.log("" + e);
          });
        },
        //
        pageSizeChange: function (val) {
          console.log(' ' + val + ' ');
          this.pageSize = val;
          var _self = this;
          _self.loadingData();//
        },
        //
        currentPageChange: function (val) {
          this.currentPage = val;
          console.log(': ' + val);
          var _self = this;
          _self.loadingData();//
        },
        //
        editSubmit: function () {
          console.info(this.editForm);
        }
      }
    }
</script>

<style scoped>
.el-breadcrumb__item{
  position: absolute;
  right: 50px;
}
  .el-breadcrumb__item span{
    cursor: pointer !important;
  }
</style>



