<template>
  <div class="main">
    <el-scrollbar class="scroll-area">
      <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
      <el-table
        :data="tableData"
        v-loading.body="loading"
        tooltip-effect="dark"
        style="width: 100%;"
        border
      >
        <el-table-column
          type="selection"
          width="35">
        </el-table-column>
        <el-table-column
          prop="orgName"
          label=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="type"
          label=""
          width="200"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="IP"
          label="IP"
          width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="code"
          label=""
          align="center"
          width="180">
        </el-table-column>
        <el-table-column
          prop="fwName"
          label=""
          align="center"
          width="180">
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination background
                     :current-page="currentPage"
                     :page-sizes="[10, 50, 100]"
                     :page-size="pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
      <!---->
    </el-scrollbar>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    data() {
      return {
        tableData: [{
          orgName: '',
          type: '',
          IP: '192.162.1.1',
          code: '80',
          fwName: 'FWMC'
        },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          }],
        //
        loading: false,
        //
        searchForm: {
          orgName: '',
          type: ''
        },
        multipleSelection: [],
        //
        currentPage: 3,
        //
        pageSize: 100,
        //
        total: 800,
        //
        deleteVisible: false,
        //
        editFormVisible: false,
        editLoading: false,
        editFormRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        //
        editForm: {
          id: 0,
          name: '',
          sex: -1,
          age: 0,
          birth: '',
          address: ''
        }
      }
    },
    mounted()
    {
      this.drawLine();
    },
    methods: {
      drawLine()
      {
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 
              type : 'shadow'        // 'line' | 'shadow'
            }
          },
          legend: {
            data:['', '', '']
          },
          calculable : true,
          xAxis : [
            {
              type : 'value'
            }
          ],
          yAxis : [
            {
              type : 'category',
              axisTick : {show: false},
              data : ['','','','','','','']
            }
          ],
          series : [
            {
              name:'',
              type:'bar',
              itemStyle : { normal: {label : {show: true, position: 'inside'}}},
              data:[200, 170, 240, 244, 200, 220, 210]
            },
            {
              name:'',
              type:'bar',
              stack: '',
              barWidth : 5,
              itemStyle: {normal: {
                  label : {show: true}
                }},
              data:[320, 302, 341, 374, 390, 450, 420]
            },
            {
              name:'',
              type:'bar',
              stack: '',
              itemStyle: {normal: {
                  label : {show: true, position: 'left'}
                }},
              data:[-120, -132, -101, -134, -190, -230, -210]
            }
          ]
        })
      }
    }
  }
</script>

<style scoped>

</style>



