        <el-menu-item index="jdgl"></el-menu-item>
        <el-menu-item index="fwqgl"></el-menu-item>
        <el-menu-item index="sjkgl"></el-menu-item>
        <el-menu-item index="sjygl"></el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-view"></i>
        </template>
        <el-menu-item index="jkzx"></el-menu-item>
        <el-menu-item index="jkfw"></el-menu-item>
        <el-menu-item index="dljd"></el-menu-item>
        <el-menu-item index="tjcx"></el-menu-item>
        <el-menu-item index="yxjk"></el-menu-item>
        <el-menu-item index="jhwljk"></el-menu-item>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
