        <el-menu-item index="sjzl"></el-menu-item>
        <el-menu-item index="szfw"></el-menu-item>
        <el-menu-item index="zdfw"></el-menu-item>
        <el-menu-item index="sjgs"></el-menu-item>
        <el-menu-item index="sfyz"></el-menu-item>
        <el-menu-item index="zdxg"></el-menu-item>
        <el-menu-item index="kzyz"></el-menu-item>
      </el-submenu>
      <el-submenu index="5">
        <template slot="title">
          <i class="el-icon-tickets"></i>
        </template>
        <el-menu-item index="sjzljc"></el-menu-item>
      </el-submenu>
      <el-submenu index="6">
        <template slot="title">
          <i class="el-icon-question"></i>
        </template>
        <el-menu-item index="wtsjfx"></el-menu-item>
      </el-submenu>
      <el-submenu index="7">
        <template slot="title">
          <i class="el-icon-news"></i>
        </template>
