<template>
  <div class="main">
    <el-scrollbar class="scroll-area">
      <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
      <el-table
        :data="tableData"
        v-loading.body="loading"
        tooltip-effect="dark"
        style="width: 100%;"
        border
      >
        <el-table-column
          type="selection"
          width="35">
        </el-table-column>
        <el-table-column
          prop="orgName"
          label=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="type"
          label=""
          width="200"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="IP"
          label="IP"
          width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="code"
          label=""
          align="center"
          width="180">
        </el-table-column>
        <el-table-column
          prop="fwName"
          label=""
          align="center"
          width="180">
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination background
                     :current-page="currentPage"
                     :page-sizes="[10, 50, 100]"
                     :page-size="pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
      <!---->
    </el-scrollbar>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    data() {
      return {
        tableData: [{
          orgName: '',
          type: '',
          IP: '192.162.1.1',
          code: '80',
          fwName: 'FWMC'
        },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          },
          {
            orgName: '',
            type: '',
            IP: '192.162.1.1',
            code: '80',
            fwName: 'FWMC'
          }],
        //
        loading: false,
        //
        searchForm: {
          orgName: '',
          type: ''
        },
        multipleSelection: [],
        //
        currentPage: 3,
        //
        pageSize: 100,
        //
        total: 800,
        //
        deleteVisible: false,
        //
        editFormVisible: false,
        editLoading: false,
        editFormRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        //
        editForm: {
          id: 0,
          name: '',
          sex: -1,
          age: 0,
          birth: '',
          address: ''
        }
      }
    },
    mounted()
    {
      this.drawLine();
    },
    methods: {
      drawLine()
      {
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption( {
          tooltip : {
            trigger: 'axis'
          },
          calculable : true,
          legend: {
            data:['','','','','','','','','']
          },
          xAxis : [
            {
              type : 'category',
              splitLine : {show : false},
              data : ['','','','','','','']
            }
          ],
          yAxis : [
            {
              type : 'value',
              position: 'right'
            }
          ],
          series : [
            {
              name:'',
              type:'bar',
              data:[320, 332, 301, 334, 390, 330, 320]
            },
            {
              name:'',
              type:'bar',
              tooltip : {trigger: 'item'},
              stack: '',
              data:[120, 132, 101, 134, 90, 230, 210]
            },
            {
              name:'',
              type:'bar',
              tooltip : {trigger: 'item'},
              stack: '',
              data:[220, 182, 191, 234, 290, 330, 310]
            },
            {
              name:'',
              type:'bar',
              tooltip : {trigger: 'item'},
              stack: '',
              data:[150, 232, 201, 154, 190, 330, 410]
            },
            {
              name:'',
              type:'line',
              data:[862, 1018, 964, 1026, 1679, 1600, 1570]
            },

            {
              name:'',
              type:'pie',
              tooltip : {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
              },
              center: [160,130],
              radius : [0, 50],
              itemStyle :{
                normal : {
                  labelLine : {
                    length : 20
                  }
                }
              },
              data:[
                {value:1048, name:''},
                {value:251, name:''},
                {value:147, name:''},
                {value:102, name:''}
              ]
            }
          ]
        })
      }
    }
  }
</script>

<style scoped>

</style>



